.heroes{
	text-align: center;
	margin: 50px;
}

.heroes h3{
	padding: 50px;
}

.allHeroes{
	display: flex;
	align-items: start;
	justify-content: center;
	gap: 50px;
	padding: 1% 7%;
}

.allHeroes > div{
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	width: 30%;
	gap: 10px;
}

.allHeroes > div > div{
	position: relative;
	cursor: pointer;
}

.allHeroes > div > div span{
	position: absolute;
	font-size: 14px;
	left: 0;
	bottom: 0;
	color:white;
	font-weight: bold;
	display: none;
}

.allHeroes > div > div:not(:hover) img{
	opacity: 0.5;
}



