h4{
	font-size: 1.25em;
	color: #f2f2f2;
	text-align: center;
	margin-bottom: 20px;
	font-weight: 200;
}

h3{
	font-size: 1.75em;
	color: #f2f2f2;
	text-align: center;
}

h3 span{
	color: #ff9a00;
}

@media (max-width: 1024px){
	.slider .left-arrow, .slider .right-arrow{
		width: 20px;
	}

	.slider .images .image img{
		width: 100px;
		height: 100px;
	}

	.image-next, .image-next-next, .image-prev, .image-prev-prev{
		display: none;
	}
}

.slider{
	width: 100%;
	display: flex;
	height: 400px;
	position: relative;
	align-items: center;
	justify-content: center;
}

.slider .left-arrow, .slider .right-arrow{
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40%;
	height: 100%;
	font-size: 5em;
	font-weight: bold;
	color:#b1b1b1;
	position: absolute;
	transition: .1s all;
	user-select: none;
}

.slider .left-arrow{
	left: 0;
}

.slider .right-arrow{
	right: 0;
}

.slider .images{
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 7%;
	gap: 10px;
	margin-top: 10em;
	transition: .1s all;
}

.slider .images .image-prev img, .slider .images .image-next img{
	width: 200px;
	height: 100px;
	margin-bottom: 400px;
}

.slider .images .image-prev-prev img, .slider .images .image-next-next img{
	width: 75px;
	height: 40px;
	margin-bottom: 550px;
}

.slider .images .image img{
	width: 700px;
	height: 350px;
}

