.flex-div{
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.navbar{
	background: #101217;
	justify-content: space-between;
	padding: 17px 2%;
	position: sticky;
	top: 0;
	color: #f2f2f2;
	z-index: 10;
}

.logo{
	gap: 10px;
	cursor: pointer;
}

.logo h3{
	font-size: 20px;
	text-shadow: 0 0 2px #b1b1b1;
}

.logo img{
	width: 30px;
	filter: drop-shadow(0 0 0.5px #b1b1b1);
}

.middle{
	flex: 1;
}

.middle ul{
	list-style-type: none;
	gap: 20px;
	justify-content: center;
}

.middle ul li{
	padding: 10px 25px;
	border: 2px solid #ff9a00;
	border-radius: 5px;
	box-shadow: 0 0 3px #ff9a00;
	font-weight: bold;
	font-size: 14px;
	text-transform: uppercase;
	letter-spacing: 1px;
	cursor: pointer;
	transform: skewX(-10deg);
	transition: .2s all;
}

.middle ul li:hover, .active{
	color:#101217;
	background: #ff9a00;
	transition: .2s all;
}

.user{
	gap: 15px;
}

.user p{
	cursor: pointer;
	color:#ff9a00;
}

.user p:hover{
	text-shadow: 0 0 2px #b1b1b1;
}
