.welcome{
	margin-top: 2em;
	width: 100%;
	padding: 2% 17%;
	text-align: center;
	color: #f2f2f2;
	line-height: 50px;
}

.welcome > span{
	text-transform: uppercase;
	font-size: 1.25em;
	letter-spacing: 1px;
}

.welcome > span > span{
	border-bottom: 2px solid white;
}

.welcome p{
	font-weight: 600;
	font-size: 2em;
	text-transform: uppercase;
	letter-spacing: 1.25px;
}

.welcome button{
	padding: 17px 30px;
	margin-top: 17px;
	transform: skew(-10deg);
	font-weight: bold;
	text-transform: uppercase;
	letter-spacing: 1px;
	color:#f2f2f2;
	background: #f07619;
	border: 2px solid #f96d00;
	border-radius: 5px;
	box-shadow: 0 0 5px #f96d00;
	cursor: pointer;
	transition: .2s all;
}

.welcome button:hover{
	background: transparent;
	color: #ff9a00;
	transition: .2s all;
}