.sides{
	margin-top: 10em;
	width: 100%;
}

h3 span{
	color: #ff9a00;
}


.article{
	padding: 0 7%;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	justify-content: space-between;
	align-items: stretch;
	color:#b1b1b1;
	font-size: 18px;
	letter-spacing: 1px;
}

.article > div{
	width: 47%;
	margin-top: 50px;
	border: 2px solid #ff9a00;
	box-shadow: 0 0 5px #ff9a00;
	background: #ff990037;
	border-radius: 10px;
}

.article h3{
	margin: 15px;
}

.article div div{
	position: relative;
}

.article p{
	width: 60%;
}

.article div img{
	width: 9em;
}

.article .left div{
	text-align: left;
	justify-content: space-around;
	padding: 1%;
}

.article .right div{
	text-align: right;
	justify-content: space-around;
	padding: 1%;
}
