.hero-card{
	color: whitesmoke;
	border: 2px solid #ff9a00;
	width: 50%;
	margin: 5em auto 10em;
	border-radius: 10px;
	box-shadow: 0 0 5px #f07619;
}

.hero-card h3{
	padding: 30px;
	border-bottom: 2px solid #ff9a00;
	background:#f07619;;
}

.display{
	display: flex;
	align-items: start;
	justify-content: left;
	text-align: left;
	padding: 30px 2%;
	gap: 30px;
	line-height: 1.5em;
	background: #ff990037;
}

